import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, onlyAlphaNum, WindowComponent } from 'summer';
import { CLASS_NAME, SituacaoTributaria } from '../../../../models/dtos/ger/situacaoTributaria';

interface SituacaoTributariaEditViewProps extends EditViewProps<SituacaoTributaria> {}

const SituacaoTributariaEditView: FC<SituacaoTributariaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<SituacaoTributaria>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  return (
    <WindowComponent title="Manutenção de Situação Tributária" wikiId={2544}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyAlphaNum(0, 2), required: true } }}
              id="codigo"
              inputProps={{ autoFocus: true }}
              isPrependLabel={true}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código."
            />
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 80 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default SituacaoTributariaEditView;
